body {
    margin: 0;
    padding: 0;
}
#root {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: brown;
}
.App {
    display: flex;
    align-items: center;
    gap: 1rem;
}
.App input {
    width: 4rem;
    height: 3rem;
    background-color: rgb(32, 31, 31);
    color: white;
    border: none;
    text-align: center;
    cursor: pointer;
}
.App button {
    width: 3rem;
    height: 2.5rem;
    cursor: pointer;
    font-weight: 400;
    font-size: 1.5rem;
}
.icon {
    background-color: rgb(215, 241, 64);
    height: 3rem;
    width: 3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 2rem;
    font-size: 2em;
    border: none;
    position: fixed;
    bottom: 2rem;
    right: 2rem;
}
.icon:hover {
    background-color: rgb(218, 236, 113);
}
.text {
    position: absolute;
    display: none;
    bottom: 60px;
    right: -10px;
    color: black;
    background-color: #e2d7d7;
    padding: 1rem;
    width: 10rem;
    text-align: center;
    font-size: 0.5em;
    font-weight: 500;
    border-radius: 1rem;
}
.icon:hover .text {
    display: block;
}